.modal, .modal-geo, .modal-franchise, .modal-about {
  width: 600px;
  height: 90vh;
  min-height: 581px;
  max-width: 450px;
  z-index: 9999;
  opacity: .9;
  background-color: #bb9ca3;
  border-radius: 8px;
  flex-direction: column;
